.detail {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    min-height: 100vh;
    height: 100%;
    background-color: rgba($color: white, $alpha: 0.79);
    z-index: 4;
    display: flex;
    align-items: center;
    justify-content: center;

        .closer {
            cursor: pointer;
            position: absolute;
            right: 0;
            top: 0;
            z-index: 5;
            background-color: black;
            color: white;
            padding: 8px 12px;
        }
    .container {
        background-color: white;
        width: 80%;
        box-shadow: 1px 9px 24px -3px rgba(0,0,0,0.2);
        -webkit-box-shadow: 1px 9px 24px -3px rgba(0,0,0,0.2);
        -moz-box-shadow: 1px 9px 24px -3px rgba(0,0,0,0.2);
        border-radius: 15px;
        overflow: hidden;
        position: relative;
        padding-bottom: 30px;
        .image-container {
            width: 100%;
            height: 160px;
            overflow: hidden;
            position: relative;
            background-color: #011064;
            img {
                width: 100%;
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
            }
            h2 {
                position: absolute;
                bottom: 36px;
                left: 36px;
                font-size: 30px;
                color: white;
                z-index: 2;
            }
        }
        .card-info {
            padding: 30px 40px 10px 40px;
            display: flex;
            flex-direction: column;
            margin-top: 10px;
            .info-row {
                display: flex;
                flex-direction: row;
                margin: 5px 0;
                img {
                    width: 16px;
                    height: 16px;
                    opacity: 0.5;
                }
                p {
                    color: #4E4E4E;
                    span {
                        color: #063F77;
                    }
                }
            }
        }
        .detail-info {
            width: 100%;
            padding: 10px 40px;
            max-height: 800px;
            overflow: auto;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            box-sizing: border-box;
            h3 {
                color: gray;
                font-size: 18px;
            }
            p {
                margin: 20px 0 10px 0;
                color: #5b5b5b;
                text-align: left;
                line-height: 28px;
            }
            span {
                text-align: left;
                line-height: 28px;
            }
        }
    }
}