.card {
    display: flex;
    flex-direction: column;
    width: 30%;
    max-width: 28%;
    padding: 15px;
    box-shadow: 1px 9px 24px -3px rgba(0,0,0,0.05);
    -webkit-box-shadow: 1px 9px 24px -3px rgba(0,0,0,0.05);
    -moz-box-shadow: 1px 9px 24px -3px rgba(0,0,0,0.05);
    margin: 20px 5px;
    border-radius: 5px;
    transition: all 0.3s ease;
    cursor: pointer;
    &:hover {
        box-shadow: 1px 9px 24px -3px rgba(0,0,0,0.2);
        -webkit-box-shadow: 1px 9px 24px -3px rgba(0,0,0,0.2);
        -moz-box-shadow: 1px 9px 24px -3px rgba(0,0,0,0.2);
    }
    .img-container {
        width: 100%;
        box-sizing: border-box;
        height: 110px;
        overflow: hidden;
        position: relative;
        background-color: #011064;
        img {
            max-width: 100%;
        }
        h2 {
            position: absolute;
            bottom: 12px;
            left: 12px;
            font-size: 30px;
            color: white;
            z-index: 2;
        }
    }
    .card-info {
        display: flex;
        flex-direction: column;
        margin-top: 10px;
        .info-row {
            display: flex;
            flex-direction: row;
            margin: 5px 0;
            img {
                width: 16px;
                height: 16px;
                opacity: 0.5;
            }
            p {
                color: #4E4E4E;
                span {
                    color: #063F77;
                }
            }
        }
    }
}